      background-color: wheat;
      z-index: 15;
      margin-top: 10px;
      border-radius: 10px;
      width: 80%;
      left: 10%;
      padding-top: 20px;
      padding-bottom: 150px; */
      position: absolute !important;
      /* background-color: rgb(97, 96, 96); */
      background-color: wheat;
      /* color: white; */
      z-index: 15;
      border-radius: 10px;
      width: 80%;
      top: 125px;
      /* margin-top: 125px; */
      left: 10%;
      padding-top: 20px;
      padding-bottom: 150px;
    }

    .b-card {
        display: inline-block;
    }

    .card-header {
        text-align: center;
    }


    .loginForm {
        width: 60%;
        position: relative;
        border-radius: 1rem;
        text-align: left;
        margin-top: 45px;
        margin-bottom: 15px;
    }

    span {
        color: #007BFF;
        cursor: pointer;
    }

     .closeBox {
        position: absolute;
        display: flex;
        top: 15px;
        right: 30px;
        z-index: 5;
    }

     .closeIco {
      padding: 5px;
