<template>
  <div class="AddAnnounComponent">
      <div class="announcModuleBg" @click.self="closeModal">
          <div class="announcModule">

            <span class="closeBox" > <span class="closeIco" @click="closeModal"> <i class="fas fa-times"></i> </span> </span>

            <b-card
                header="Dodaj ogłoszenie"
                header-bg-variant="primary"
                header-text-variant="white"
                class="announcForm"
            >
                <b-card-text>
                    <b-form @submit="onSubmit">
                        <b-form-group
                            description="Podaj tytuł swojego ogłoszenia."
                            label="Tytuł ogłoszenia"
                        > 
                            <b-form-input v-model="title" required> </b-form-input>
                        </b-form-group> 

                        <b-form-group
                            description="Podaj opis swojego ogłoszenia."
                            label="Opis ogłoszenia"
                        > 
                             <b-form-textarea
                                id="textarea"
                                v-model="text"
                                rows="2"
                                max-rows="2"
                            ></b-form-textarea>
                        </b-form-group> 

                        <b-form-group
                            description="Podaj link do zdjęcia, które ma się wyświetlać jako miniaturka."
                            label="Zdjęcie"
                        > 
                        <b-form-input v-model="url" > </b-form-input>
                        </b-form-group>

                        <b-form-group
                            description="Wybierz kategorię odpowiadającą twojemu ogłoszeniu"
                            label="Kategoria ogłoszenia"
                        >
                            <b-form-select v-model="category" :options="options" ></b-form-select>
                        </b-form-group>


                    <b-form-group class="text-center">
                        <b-button type="submit" variant="outline-primary" :disabled="acceptableSubmittion" >Zamieść ogłoszenie</b-button>
                    </b-form-group>

                    </b-form>
                </b-card-text>
                
            </b-card>
          </div>
      </div>
  </div>
</template>

<script>

import AnnouncementsService from '../AnnouncementsService';

export default {
    name: 'AddAnnounComponent',

    data() {
        return {
            title: '',
            text: '',
            url: '',
            category: [],
            options: [
                { value: 'cars', text: 'Motoryzacja' },
                { value: 'clothes', text: 'Ubrania' },
                { value: 'houses', text: 'Nieruchomości' },
                { value: 'services', text: 'Usługi' },
                { value: 'other', text: 'inne'}
            ]
        }
    },
    methods: {
        onSubmit (e) {
            e.preventDefault();
            AnnouncementsService.insertAnnouncement(this.url, this.title, this.text, this.category)

            alert('udał się zamieścić twój post!')
            
            this.$emit('close')

        },
        closeModal() {
            this.$emit('close');
        },
        checked() {
            if(this.category.length > 1) {
                this.category.splice(1,1)
            }
        }
    },
    mounted() {
        this.checked();
    },
    computed: {
        acceptableSubmittion() {
            return this.title.length > 2 && this.text.length > 3 && this.category.length > 0 ? false : true;
        }
    }

}
</script>

<style>
    .announcModuleBg {
        position: fixed;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.4);
        top: 0;
        left: 0;
        cursor: pointer;
    }

    .announcModule {
        position: absolute;
        background-color: #e2ebec;
        border-radius: 1.5rem;
        top: 120px;
        left: 10%; right: 10%;
        width: 80%;
        height: 83%;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: default;
    }

    .announcForm {
        width: 60%;
        position: relative;
        border-radius: 1rem;
        text-align: left;
        margin-top: 10px;
        margin-bottom: 15px;
    }

    span {
        color: #007BFF;
        cursor: pointer;
    }

    .closeBox {
        position: absolute;
        display: flex;
        top: 15px;
        right: 30px;
        z-index: 5;
    }

     .closeIco {
      padding: 5px;
      width: 30px; 
      font-size: 25px;
      cursor: pointer;
      color: #3f3f3f;
    }

    .closeIco:hover {
      color: #202020;
    }

    @media(max-width: 450px) {

        .registerModule {
            left: 5%; right: 5%;
            width: 90%;
         }
     
        .registerForm {
            width: 85%;
        }

        .h6Text {
            margin-right: 100px;
        }
    }
</style>